<!--  -->
<template>
    <div class="container">
        <div class="header">
            <a href="javascript:;"><router-link to="/"><dv-decoration-5></dv-decoration-5></router-link></a>
        </div>
        <el-row class="body" :gutter="0">
            <el-col :span="6" class="left">
                <dv-border-box-13 class="left-top">
                    <p>数据展示1</p>
                    <div ref="leftTop" id="leftTop"></div>
                </dv-border-box-13>
                <dv-border-box-13 class="left-middle">
                    <p>数据展示2</p>
                    <div ref="leftMiddle" id="leftMiddle"></div>
                </dv-border-box-13>
                <dv-border-box-13 class="left-down">
                    <p>数据展示3</p>
                    <div ref="leftDown" id="leftDown"></div>
                </dv-border-box-13>
            </el-col>
            <el-col :span="12" class="middle">
                <dv-border-box-13 class="middle-top">
                    <div ref="middleTop" id="middleTop"></div>
                </dv-border-box-13>

                <dv-scroll-board :config="config" id="middleDown" @click="addData()"/>

            </el-col>
                
            <el-col :span="6" class="right">

                <dv-border-box-13 class="right-top">
                    <p>数据展示4</p>
                    <div ref="rightTop" id="rightTop"></div>
                </dv-border-box-13>

                <dv-border-box-13 class="right-middle">
                    <p>数据展示5</p>
                    <div ref="rightMiddle" id="rightMiddle"></div>
                </dv-border-box-13>

                <dv-border-box-13 class="right-down">
                    <p>数据展示6</p>
                    <div ref="rightDown" id="rightDown"></div>
                </dv-border-box-13>

            </el-col>
        </el-row>
    </div>
</template>


<script>
import 'echarts-gl'
export default {
    name: 'DashBoard',
    data() {
        return {
            data:[],
        }
    },
    methods: {
        createChart(dom, options) {
            let chart = this.$echarts.init(dom);
            chart.setOption(options);
            return chart;
        },
        addData() {
            let num = Math.floor(100*Math.random())
            if (num < 10) this.data.push([`歼${num}`, `西南战区`, `<span style="color:red">未连接</span>`])
            else this.data.push([`歼${num}`, `西南战区`, `<span style="color:lightgreen">已连接</span>`])
        }
    },

    computed: {
        dataShow() {
            let endIndex = this.data.length
            if (endIndex < 5) {
                return this.data
            }
            else {
                return this.data.slice(endIndex-5, endIndex)
            }
        },
        config() {
            return {
                header: ['战机编号', '所属战区', '当前状态'],
                data: this.dataShow,
                hoverPause: true,
                headerBGC: '#1939a0',
                oddRowBGC: '#0948a3',
                evenRowBGC: '#185fbb'
            }
        },
    },

    mounted() {
        setInterval(() => {
            this.addData()
        }, 1000)

        window.addEventListener('resize', () => {
            lt.resize()
            lm.resize()
            ld.resize()
            mt.resize()
            rt.resize()
            rm.resize()
            rd.resize()
        })

        let lt = this.createChart(this.$refs.leftTop, {
            series: [
                {
                type: 'pie',
                data: [
                    {
                        value: 335,
                        name: 'A'
                    },
                    {
                        value: 234,
                        name: 'B'
                    },
                    {
                        value: 1548,
                        name: 'C'
                    }
                ],
                radius: ['40%', '70%']
                }
            ]
        })

        let lm = this.createChart(this.$refs.leftMiddle, {
            xAxis: {
                data: ['A', 'B', 'C', 'D', 'E'],
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
            },
            yAxis: {
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
            },
            series: [
                {
                    data: [10, 22, 28, 23, 19],
                    type: 'line',
                    areaStyle: {}
                },
                {
                    data: [25, 14, 23, 35, 10],
                    type: 'line',
                    areaStyle: {
                        color: '#ff0',
                        opacity: 0.5
                    }
                }
            ]
        })

        let ld = this.createChart(this.$refs.leftDown, {
            xAxis: {
                data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子'],
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
            },
            yAxis: {
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
            },
            series: [
            {
                name: '销量',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }
            ]
        })

        let mt = this.createChart(this.$refs.middleTop, {
            // backgroundColor: '#000',
            globe: {
                baseTexture: '../earth/earth.jpg',
                heightTexture: '../earth/bathymetry_bw_composite_4k.jpg',
                displacementScale: 0.1,
                shading: 'lambert',
                // environment: '../earth/starfield.jpg',
                light: {
                    ambient: {
                        intensity: 0.8
                    },
                    main: {
                        intensity: 1
                    }
                },
                layers: [
                {
                    type: 'blend',
                    blendTo: 'emission',
                    texture: '../earth/night.jpg'
                },
                {
                    type: 'overlay',
                    texture: '../earth/clouds.png',
                    shading: 'lambert',
                    distance: 5
                }
                ]
            },
            series: []
        })

        let rt = this.createChart(this.$refs.rightTop, {
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                type: 'cross',
                label: {
                    backgroundColor: '#6a7985'
                },
                
                }
            },
            legend: {
                data: ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine'],
                textStyle: {
                    color: '#fff',
                },
            },
            toolbox: {
                feature: {
                saveAsImage: {}
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
                    axisLabel: {
                        textStyle: {
                            color: '#fff' 
                        }
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    axisLabel: {
                        textStyle: {
                            color: '#fff' 
                        }
                    }
                }
            ],
            series: [
                {
                    name: 'Email',
                    type: 'line',
                    stack: 'Total',
                    areaStyle: {},
                    emphasis: {
                        focus: 'series'
                    },
                    data: [120, 132, 101, 134, 90, 230, 210],
                    
                },
                {
                    name: 'Union Ads',
                    type: 'line',
                    stack: 'Total',
                    areaStyle: {},
                    emphasis: {
                        focus: 'series'
                    },
                    data: [220, 182, 191, 234, 290, 330, 310]
                },
                {
                    name: 'Video Ads',
                    type: 'line',
                    stack: 'Total',
                    areaStyle: {},
                    emphasis: {
                        focus: 'series'
                    },
                    data: [150, 232, 201, 154, 190, 330, 410]
                },
                {
                    name: 'Direct',
                    type: 'line',
                    stack: 'Total',
                    areaStyle: {},
                    emphasis: {
                        focus: 'series'
                    },
                    data: [320, 332, 301, 334, 390, 330, 320]
                },
                {
                    name: 'Search Engine',
                    type: 'line',
                    stack: 'Total',
                    label: {
                        show: true,
                        position: 'top'
                    },
                    areaStyle: {},
                    emphasis: {
                        focus: 'series'
                    },
                    data: [820, 932, 901, 934, 1290, 1330, 1320]
                }
            ]
        })

        let rm = this.createChart(this.$refs.rightMiddle, {
            // legend: {
            //     data: ['Allocated Budget', 'Actual Spending'],
            //     textStyle: {
            //         color: '#fff',
            //     },
            // },
            radar: {
                shape: 'circle',
                indicator: [
                    { name: 'Sales', max: 6500 },
                    { name: 'Administration', max: 16000 },
                    { name: 'Information Technology', max: 30000 },
                    { name: 'Customer Support', max: 38000 },
                    { name: 'Development', max: 52000 },
                    { name: 'Marketing', max: 25000 }
                ]
            },
            series: [
                {
                type: 'radar',
                data: [
                    {
                        value: [4200, 3000, 20000, 35000, 50000, 18000],
                        name: 'Allocated Budget'
                    },
                    {
                        value: [5000, 14000, 28000, 26000, 42000, 21000],
                        name: 'Actual Spending'
                    }
                ]
                }
            ]
        })

        let rd = this.createChart(this.$refs.rightDown, {
            dataset: {
                source: [
                    ['score', 'amount', 'product'],
                    [89.3, 58212, 'Matcha'],
                    [57.1, 78254, 'Tea'],
                    [74.4, 41032, 'Cocoa'],
                    [50.1, 12755, 'Brownie'],
                    [89.7, 20145, 'Matcha'],
                    [68.1, 79146, 'Tea'],
                    [19.6, 91852, 'Juice'],
                    [10.6, 101852, 'Lemon'],
                    [32.7, 20112, 'Brownie']
                ]
            },
            xAxis: {
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
            },
            yAxis: { 
                type: 'category',
                axisLabel: {
                    textStyle: {
                        color: '#fff' 
                    }
                }
             },
            series: [
                {
                type: 'bar',
                encode: {
                    // 将 "amount" 列映射到 X 轴。
                    x: 'amount',
                    // 将 "product" 列映射到 Y 轴。
                    y: 'product'
                }
                }
            ]
        })

    },
}
</script>


<style scoped lang="less">
    .container{
        // display:block;
        width:100vw;
        height:100vh;
        margin: 0;
        background-image: url('../assets/bg.png');

        // background-color: midnightblue;

        .header{
            width: 40%;
            height: 5%;
            margin-left: 30%; 
        }

        .body {
            display: flex;
            width: 100%;
            height: 95%;

            p {
                font-size:1vw;
                color: white;
                margin: 2% 0 0 0;
                text-align: center;
                // margin-top:5%;
            }

            .left{
                .left-top {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    margin-bottom: 5%;
                    // background-color: red;

                    #leftTop{
                        width: 100%;
                        height: 90%;
                    }
                }

                .left-middle {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    margin-bottom: 5%;
                    // background-color: red;

                    #leftMiddle{
                        width: 100%;
                        height: 100%;
                    }
                }

                .left-down {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    // background-color: red;
                    #leftDown{
                        width: 100%;
                        height: 100%;
                    }

                }
                
                // * {
                //     margin-bottom: 5%
                // }

            }

            .middle {
                .middle-top {
                    display: flex;
                    width: 100%;
                    height: 62%;
                    margin-bottom: 3%;
                    #middleTop {
                        width: 100%;
                        height: 80%;
                        // margin-top: 10%;
                        margin: 5% auto;
                    }
                }
                #middleDown {
                    width: 98%;
                    height: 30%;
                    margin-left: 1%;
                    margin-top: 0%
                }
            }

            .right {
                .right-top {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    margin-bottom: 5%;
                    // background-color: red;

                    #rightTop{
                        width: 100%;
                        height: 90%;
                        // margin-top: 15%;
                        // align-self: center;
                        // background-color: aqua;
                    }
                }

                .right-middle {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    margin-bottom: 5%;
                    // background-color: red;

                    #rightMiddle{
                        width: 100%;
                        height: 80%;
                        // margin-top: 10%
                    }
                }

                .right-down {
                    display: flex;
                    width: 100%;
                    height: 30%;
                    // background-color: red;
                    #rightDown{
                        width: 90%;
                        height: 100%;
                        margin-left: 5%;
                    }

                }
            }
        }

    }
</style>